
import Vue from 'vue'
import SlimSelect from '@/slim-select'

export default Vue.extend({
  data() {
    return {
      butts: '',
      beforeOnChangeSingle: null,
      beforeOnChangeMultiple: null,
      onChangeSingle: null,
      onChangeMultiple: null
    }
  },
  mounted() {
    new SlimSelect({
      select: '#dataSingle',
      data: [
        {text: 'data placeholder', placeholder: true},
        {text: 'Value 2'},
        {text: 'Value 3', value: ''}
      ]
    })

    new SlimSelect({
      select: '#dataMultiple',
      data: [
        {placeholder: true, text: 'data placeholder'},
        {text: 'Human'},
        {
          label: 'Animals',
          options: [
            {text: 'Cat'},
            {text: 'Dog'},
            {text: 'Bird', value: ''}
          ]
        }
      ]
    })
    
    const ajaxSingle = new SlimSelect({
      select: '#ajaxSingle',
      placeholder: 'Search "Graham"',
      searchingText: 'Searching Users...',
      ajax(search: any, callback: any) {
        if (search.length < 3) {
          callback('Need 3 characters')
          return
        }

        fetch('https://jsonplaceholder.typicode.com/users')
          .then(function(response) {
            return response.json()
          })
          .then(function(json) {
            const data = [] as any
            for (let i = 0; i < json.length; i++) {
              data.push({ text: json[i].name })
            }

            setTimeout(() => {
              callback(data)
            }, 1000)
          })
          .catch(function(error) {
            callback(false)
          })
      }
    })

    const ajaxMulti = new SlimSelect({
      select: '#ajaxMultiple',
      placeholder: 'Search "Dennis"',
      ajax(search: any, callback: any) {
        fetch('https://jsonplaceholder.typicode.com/users')
          .then(function(response) {
            return response.json()
          })
          .then(function(json) {
            const data = [] as any
            for (let i = 0; i < json.length; i++) {
              data.push({ text: json[i].name })
            }

            setTimeout(() => {
              ajaxMulti.setSearchText('Sorry No Results.')
              callback(data)
            }, 1000)
          })
          .catch(function(error) {
            callback(false)
          })
      }
    })

    new SlimSelect({
      select: '#placeholderSingle',
      placeholder: 'Placeholder Text Here'
    })

    new SlimSelect({
      select: '#placeholderMultiple',
      placeholder: 'Placeholder Text Here'
    })

    new SlimSelect({
      select: '#allowDeselect',
      allowDeselect: true
    })

    new SlimSelect({
      select: '#addableSingle',
      addable: (value: string) => {
        return value
      }
    })

    new SlimSelect({
      select: '#addableMultiple',
      addable: (value: string) => {
        return {
          text: value,
          value: value.toLowerCase()
        }
      }
    })

    new SlimSelect({
      select: '#selectMultiLimit',
      limit: 2
    })

    new SlimSelect({
      select: '#select-class'
    })

    new SlimSelect({
      select: '#selectInnerHTMLSingle',
      searchHighlight: true,
      data: [
        {
          innerHTML: '<b>Bold Text</b>',
          text: 'Bold Text',
          value: 'bold text'
        },
        {
          innerHTML: '<i>Slim Select you are awesome</i>',
          text: 'Slim Select awesome'
        },
        {
          innerHTML: '<div style="border: solid 1px #666666;">Border</div>',
          text: 'Border',
          value: 'border'
        }
      ]
    })

    new SlimSelect({
      select: '#selectInnerHTMLMulti',
      data: [
        {
          innerHTML: '<b>Bold Text</b>',
          text: 'Bold Text',
          value: 'bold text'
        },
        {
          innerHTML: '<i>Slim Select you are awesome</i>',
          text: 'Slim Select awesome'
        },
        {
          innerHTML: '<div style="border: solid 1px #666666;">Border</div>',
          text: 'Border',
          value: 'border'
        }
      ]
    })

    new SlimSelect({
      select: '#selectInnerHTMLSingleText',
      valuesUseText: true,
      data: [
        {
          innerHTML: '<b>Bold Text</b>',
          text: 'Bold Text',
          value: 'bold text'
        },
        {
          innerHTML: '<i>Slim Select you are awesome</i>',
          text: 'Slim Select awesome'
        },
        {
          innerHTML: '<div style="border: solid 1px #666666;">Border</div>',
          text: 'Border',
          value: 'border'
        }
      ]
    })

    new SlimSelect({
      select: '#selectInnerHTMLMultiText',
      valuesUseText: true,
      data: [
        {
          innerHTML: '<b>Bold Text</b>',
          text: 'Bold Text',
          value: 'bold text'
        },
        {
          innerHTML: '<i>Slim Select you are awesome</i>',
          text: 'Slim Select awesome'
        },
        {
          innerHTML: '<div style="border: solid 1px #666666;">Border</div>',
          text: 'Border',
          value: 'border'
        }
      ]
    })

    new SlimSelect({
      select: '#showSearch',
      showSearch: false
    })

    new SlimSelect({
      select: '#searchText',
      searchText: 'Sorry nothing to see here'
    })

    new SlimSelect({
      select: '#searchPlaceholder',
      searchPlaceholder: 'Search for the good stuff!'
    })

    new SlimSelect({
      select: '#searchHighlight',
      searchHighlight: true
    })

    new SlimSelect({
      select: '#closeOnSelectSingle',
      closeOnSelect: false
    })

    new SlimSelect({
      select: '#closeOnSelectMultiple',
      closeOnSelect: false
    })

    new SlimSelect({
      select: '#showContentUp',
      showContent: 'up'
    })

    new SlimSelect({
      select: '#showContentDown',
      showContent: 'down'
    })

    new SlimSelect({
      select: '#beforeOnChangeSingle',
      beforeOnChange: (value: any) => {
        this.beforeOnChangeSingle = value
      }
    })

    new SlimSelect({
      select: '#beforeOnChangeMultiple',
      beforeOnChange: (values: any) => {
        this.beforeOnChangeMultiple = values
      }
    })

    new SlimSelect({
      select: '#onChangeSingle',
      onChange: (value: any) => {
        this.onChangeSingle = value
      }
    })

    new SlimSelect({
      select: '#onChangeMultiple',
      onChange: (value: any) => {
        this.onChangeMultiple = value
      }
    })

    new SlimSelect({
      select: '#showOptionTooltips',
      showOptionTooltips: true
    })
  }
})
